import axios from "./axios";
const au = document.cookie.split(';').filter(value => value.indexOf("VerifyT") >= 0)[0];
let headers =  {
    'Authorization' :  typeof(au) === "string" ? au.slice(8) : ""
};

/**
 * 
 * @param {string} h : header{key: value} 
 */
export const setHeader = (h) => {
    headers = h;
}

export const getMenu = (param) => {
    return axios.request({
        url: "/permission/getMenu",
        method: "post",
        data: param,
        headers
    })  
}

export const getData = () => {
    return axios.request({
        url: '/home/data',
        headers
    })
}